.who-suits {
  &__title {
    margin-bottom: calc(var(--gap) * 12.5);

    @include ipad() {
      margin-bottom: calc(var(--gap) * 3);
    }
  }
  &__content {
    display: flex;
    max-width: 894px;
    width: 100%;
    margin: 0 auto;

    @include laptop() {
      flex-direction: column;
      align-items: center;
    }
  }

  &__block {
    flex: 0 0 407px;
    width: 407px;
    margin-right: calc(var(--gap) * 8);
    box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.1);
    border-radius: 16px;
    overflow: hidden;
    background-color: var(--white);

    @include laptop() {
      flex: 1 1 auto;
      width: 100%;
      max-width: 607px;
      margin-right: 0;
      margin-bottom: calc(var(--gap) * 3);
    }

    &:last-child {
      margin-right: 0;
    }

    &--main {
      .who-suits__header {
        background-color: var(--main-color);
      }
    }
  }

  &__header {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #2d2d2d;
    height: 44px;
    color: var(--white);
  }

  &__header-text {
    font-weight: 700;
    font-size: 14px;
    line-height: 20px;
  }

  &__header-icon {
    width: 24px;
    height: 24px;
    margin-right: 4px;

    svg {
      fill: var(--white);
    }
  }

  &__body {
    padding: calc(var(--gap) * 3);
  }

  &__main-info {
    padding-bottom: calc(var(--gap) * 3);
    margin-bottom: calc(var(--gap) * 3);
    border-bottom: 1px solid var(--gray-color);
  }

  &__main-prise {
    display: flex;
    align-items: flex-end;
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
    margin-top: calc(var(--gap) * 2);
  }

  &__main-title {
    margin-bottom: calc(var(--gap) * 2);
    height: 64px;

    &--mb {
      margin-bottom: calc(var(--gap) * 4);
    }
  }

  &__main-prise-number {
    font-weight: 400;
    font-size: 60px;
    line-height: 80%;
  }

  &__title-block {
    font-weight: 700;
    font-size: 20px;
    line-height: 24px;
    margin-bottom: calc(var(--gap) * 3);
  }

  &__item {
    display: flex;
    align-items: center;
    margin-bottom: calc(var(--gap) * 2);

    &:last-child {
      margin-bottom: 0;
    }
  }

  &__item-icon {
    width: 24px;
    height: 24px;
    margin-right: 4px;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
  }

  &__main-messages {
    width: 64px;
    height: 64px;
    margin-bottom: calc(var(--gap) * 4);
  }

  &__box {
    display: flex;
    align-items: center;
    background: var(--bg-color);
    border-radius: 5px;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    color: var(--main-color);
    fill: var(--main-color);
    padding: calc(var(--gap) * 2);
    margin-bottom: var(--gap);
  }

  &__box-icon {
    width: 24px;
    height: 24px;
    margin-right: 4px;
  }

  &__plus {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 24px;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    margin-bottom: var(--gap);
  }
}
