.text {
  color: --black-color;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;

  @include ipad() {
    font-size: 14px;
    line-height: 20px;
  }

  a {
    text-decoration: underline;
  }

  &--big {
    font-size: 20px;
    line-height: 30px;

    @include ipad() {
      font-size: 14px;
      line-height: 20px;
    }
  }

  &--small {
    font-size: 14px;
    line-height: 20px;
  }

  &--mb-16 {
    margin-bottom: calc(var(--gap) * 2);

    @include phone() {
      margin-bottom: var(--gap);
    }
  }
}
