.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
  display: flex;
  align-items: center;
  flex: 0 0 auto;
  height: 80px;
  width: 100%;
  background-color: var(--white);
  box-shadow: var(--shadow-header);
  z-index: 100;

  @include phone() {
    height: 54px;
  }

  &__aside-buttons {
    display: flex;
    align-items: center;
  }

  &__container {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &__logo {
    position: relative;
    width: 126px;
    height: 40px;
    flex: 0 0 auto;

    @include phone() {
      width: 102px;
      height: 33px;
    }
  }
}
