.how-help {
  display: flex;

  @include phone() {
    flex-direction: column;
  }

  &__title-main {
    margin-bottom: calc(var(--gap) * 9.5);
  }

  &__col {
    flex: 1 1 auto;
    width: 50%;

    @include phone() {
      width: 100%;
    }
  }

  &__divider {
    width: 1px;
    align-self: stretch;
    align-self: stretch;
    background-color: var(--gray-color);
    margin: calc(var(--gap) * 6) calc(var(--gap) * 3.5) calc(var(--gap) * 1.5);
    flex: 0 0 auto;

    @include phone() {
      width: 100%;
      height: 0px;
      margin: calc(var(--gap) * 4) 0 calc(var(--gap) * 1.5);
    }
  }

  &__title {
    text-align: center;
    font-weight: 700;
    font-size: 20px;
    line-height: 24px;
    margin-bottom: calc(var(--gap) * 3);
  }

  &__cell-wpap {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -16px -16px;

    @include desctopSmall() {
      flex-direction: column;
    }
  }

  &__cell {
    flex: 0 0 50%;
    padding: 0 calc(var(--gap) * 1.5) calc(var(--gap) * 3);
  }

  &__box {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: calc(var(--gap) * 3);
    border-radius: 20px;
    border: 2px dashed var(--gray-color);
    height: 148px;

    @include phone() {
      padding: calc(var(--gap) * 2) calc(var(--gap) * 1);
      height: auto;
    }
  }

  &__cell-icon {
    width: 32px;
    height: 32px;
    margin-bottom: calc(var(--gap) * 2);

    @include phone() {
      margin-bottom: 10px;
    }
  }

  &__cell-text {
    font-weight: 700;
    font-size: 16px;
    line-height: 24px;
    text-align: center;
  }
}
