.section-main {
  position: relative;
  z-index: 2;
  &__title {
    margin-bottom: calc(var(--gap) * 4);

    @include desctop() {
      margin-bottom: calc(var(--gap) * 3);
    }

    @include ipad() {
      margin-bottom: calc(var(--gap) * 2);
    }
  }

  &__content {
    height: 440px;

    @include desctop() {
      height: 292px;
    }

    @include ipad() {
      height: 192px;
    }
  }

  &__buttons {
    margin-bottom: var(--gap);

    @include desctopSmall() {
      flex-direction: column;
    }

    @include phone() {
      margin-bottom: 0;
    }

    .col {
      @include desctopSmall() {
        width: 250px;
      }

      @include ipad() {
        width: 100%;
      }
    }
  }

  &__comment {
    display: flex;
    align-items: center;
  }

  &__comment-icon {
    width: 24px;
    height: 24px;
    fill: var(--black-color);
    margin-right: var(--gap);
  }
}
