  .section--main:after {
    top: auto;
    bottom: 10px;
    left: 50%;
    right: auto;
    width: 100%;
    height: 352px;
    max-width: 100%;
    background-position: bottom center;
    transform: translateX(-50%);
    background-image: url("../../img/section-main/mobile/section-main-img.png");
  }

  .section--main .section__decor-right:after {
    top: auto;
    right: 0;
    bottom: 0;
    left: 0;
    height: 500px;
    width: 100%;
    background-position: bottom left;
    background-image: url("../../img/section-main/mobile/ellipse-right.svg");
  }

  .section--main .section__decor-right:before {
    display: none;
  }

  .text {
    font-size: 14px;
    line-height: 20px;
  }

  .text--big {
    font-size: 14px;
    line-height: 20px;
  }

  .title {
    font-size: 24px;
    line-height: 36px;
  }

  .what-have__accordeon-item {
    display: block;
  }

  .what-have__img-mobile {
    margin-top: calc(var(--gap) * 3);
  }

  .who-suits__title {
    margin-bottom: calc(var(--gap) * 3);
  }
}

@media screen and (max-width: 576px) {
  .button-mobile-menu {
    margin-left: var(--gap);
  }

  .button {
    padding: calc(var(--gap) * 2) calc(var(--gap) * 3);
  }

  .button--header {
    padding: 10px calc(var(--gap) * 3);
    height: 40px;
  }

  .button--wide-mobile {
    width: 100%;
  }

  .header {
    height: 54px;
  }

  .header__logo {
    width: 102px;
    height: 33px;
  }

  .how-help {
    flex-direction: column;
  }

  .how-help__col {
    width: 100%;
  }

  .how-help__divider {
    width: 100%;
    height: 0px;
    margin: calc(var(--gap) * 4) 0 calc(var(--gap) * 1.5);
  }

  .how-help__box {
    padding: calc(var(--gap) * 2) calc(var(--gap) * 1);
    height: auto;
  }

  .how-help__cell-icon {
    margin-bottom: 10px;
  }

  .nav {
    top: 53px;
    height: calc(100% - 53px);
  }

  .section-main__buttons {
    margin-bottom: 0;
  }

  .section-statistics__title {
    text-align: start;
    margin-bottom: calc(var(--gap) * 2);
  }

  .section-statistics__delimiter {
    padding: 0;
    font-size: 16px;
    line-height: 24px;
    margin-bottom: calc(var(--gap) * 5.4);
  }

  .section-statistics__delimiter div {
    margin: 0;
  }

  .section-statistics__delimiter:after,
  .section-statistics__delimiter:before {
    display: none;
  }

  .section-statistics__expenses-button:after {
    line-height: 41px;
  }

  .section-statistics__expenses-content {
    height: auto;
  }

  .section {
    padding: calc(var(--gap) * 6) 0 0;
  }

  .section--main {
    height: 850px;
  }

  .section--main .section-wrapper {
    padding: calc(var(--gap) * 10) 0 0;
  }

  .section--statistics {
    padding: calc(var(--gap) * 13) 0 calc(var(--gap) * 2);
  }

  .section--what-have {
    padding: calc(var(--gap) * 3) 0 calc(var(--gap) * 4);
  }

  .section--action-plan {
    padding: calc(var(--gap) * 6) 0;
    border-bottom: 1px solid var(--gray-color);
  }

  .section--who-suits {
    padding: calc(var(--gap) * 6) 0;
  }

  .section--slider {
    padding: calc(var(--gap) * 6) 0;
  }

  .text--mb-16 {
    margin-bottom: var(--gap);
  }
}

@media print {
