.section {
  padding: calc(var(--gap) * 10) 0 0;
  overflow: hidden;
  position: relative;

  .section-wrapper {
    position: relative;
  }

  @include phone() {
    padding: calc(var(--gap) * 6) 0 0;
  }

  &--main {
    height: 960px;
    background-color: var(--light-blue-color);

    @include desctop() {
      height: 720px;
    }

    @include ipad() {
      height: 894px;
    }

    @include phone() {
      height: 850px;
    }

    &:after {
      content: "";
      position: absolute;
      top: 52.3%;
      transform: translate(0, -50%);
      right: 41px;
      background-size: contain;
      background-repeat: no-repeat;
      background-image: url("../../img/section-main/desctop/section-main-img.png");
      width: 932px;
      height: 788px;

      @include desctopBig() {
        width: 728px;
        height: 620px;
        right: 21px;
      }

      @include desctop() {
        width: 578px;
        height: 489px;
        right: -48px;
        top: 430px;
      }

      @include desctopSmall() {
        width: 500px;
        height: 400px;
        right: -58px;
        top: 500px;
      }

      @include ipad() {
        top: auto;
        bottom: 10px;
        left: 50%;
        right: auto;
        width: 100%;
        height: 352px;
        max-width: 100%;
        background-position: bottom center;
        transform: translateX(-50%);
        background-image: url("../../img/section-main/mobile/section-main-img.png");
      }
    }

    &:before {
      content: "";
      position: absolute;
      bottom: 0px;
      left: 0px;
      background-size: contain;
      background-repeat: no-repeat;
      background-position: bottom left;
      background-image: url("../../img/section-main/desctop/ellipse-left.svg");
      width: 389px;
      height: 562px;
      @include desctop() {
        display: none;
      }
    }

    & .section-wrapper {
      padding: calc(var(--gap) * 22) 0 0;

      @include desctop() {
        padding: calc(var(--gap) * 13) 0 0;
      }

      @include desctopSmall() {
        padding: calc(var(--gap) * 9) 0 0;
      }

      @include phone() {
        padding: calc(var(--gap) * 10) 0 0;
      }
    }

    & .section__decor-right {
      &:after {
        content: "";
        position: absolute;
        top: 0;
        right: -6px;
        background-size: contain;
        background-repeat: no-repeat;
        background-position: top right;
        background-image: url("../../img/section-main/desctop/ellipse-right.svg");
        width: 688px;
        height: 646px;

        @include desctop() {
          width: 293px;
          height: 358px;
          top: 79px;
          right: 0px;
        }

        @include desctopSmall() {
          right: -100px;
        }

        @include ipad() {
          top: auto;
          right: 0;
          bottom: 0;
          left: 0;
          height: 500px;
          width: 100%;
          background-position: bottom left;
          background-image: url("../../img/section-main/mobile/ellipse-right.svg");
        }
      }

      &:before {
        content: "";
        position: absolute;
        top: 12px;
        right: 0px;
        background-size: contain;
        background-repeat: no-repeat;
        background-position: top right;
        background-image: url("../../img/section-main/desctop/triangle.svg");
        width: 724px;
        height: 947px;

        @include desctop() {
          width: 277px;
          height: 464px;
          top: 236px;
        }

        @include ipad() {
          display: none;
        }
      }
    }
  }

  &--statistics {
    padding: calc(var(--gap) * 13) 0 var(--gap);
    background: linear-gradient(180deg, #4547a8 24.48%, #5759ce 100%);
    min-height: 630px;

    @include phone() {
      padding: calc(var(--gap) * 13) 0 calc(var(--gap) * 2);
    }
  }

  &--how-help {
    padding: calc(var(--gap) * 13) 0 calc(var(--gap) * 14);
  }

  &--what-have {
    background-color: var(--white);
    padding: calc(var(--gap) * 12.5) 0 calc(var(--gap) * 7);

    @include phone() {
      padding: calc(var(--gap) * 3) 0 calc(var(--gap) * 4);
    }
  }

  &--action-plan {
    background-color: var(--white);
    padding: 0;
    border-top: 1px solid var(--gray-color);

    @include phone() {
      padding: calc(var(--gap) * 6) 0;
      border-bottom: 1px solid var(--gray-color);
    }
  }

  &--who-suits {
    padding: calc(var(--gap) * 12.5) 0 calc(var(--gap) * 15);

    @include laptop() {
      padding: calc(var(--gap) * 8) 0 calc(var(--gap) * 6);
    }

    @include phone() {
      padding: calc(var(--gap) * 6) 0;
    }
  }

  &--cases {
    background-color: var(--white);
    padding: calc(var(--gap) * 12.5) 0 calc(var(--gap) * 9);

    @include desctop() {
      margin-bottom: 0;
    }

    @include laptop() {
      padding: calc(var(--gap) * 2) 0 calc(var(--gap) * 2);
    }
  }

  &--slider {
    background-color: var(--white);
    border-top: 1px solid var(--gray-color);
    padding: calc(var(--gap) * 12.5) 0 calc(var(--gap) * 13);

    @include phone() {
      padding: calc(var(--gap) * 6) 0;
    }
  }

  &--footer {
    color: var(--white);
    background-color: var(--bg-dark-color);
    padding: calc(var(--gap) * 11) 0 calc(var(--gap) * 2);
  }
}
