.button {
  display: inline-flex;
  align-items: center;
  font-style: normal;
  position: relative;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  background-color: var(--main-color);
  color: var(--white);
  padding: calc(var(--gap) * 1) calc(var(--gap) * 3);
  height: 48px;
  border-radius: var(--border-radius);
  transition: background-color 0.3s;
  white-space: nowrap;
  justify-content: center;
  flex: 0 0 auto;
  cursor: pointer;

  &__icon {
    fill: inherit;
    width: 24px;
    height: 24px;
  }

  @include phone() {
    padding: calc(var(--gap) * 2) calc(var(--gap) * 3);
  }

  &--header {
    @include phone() {
      padding: 10px calc(var(--gap) * 3);
      height: 40px;
    }
  }

  &--wide {
    width: 100%;
  }

  &--wide-mobile {
    @include phone() {
      width: 100%;
    }
  }

  &:hover {
    background-color: var(--main-color-light);
  }

  &:active {
    background-color: var(--main-color-deep);
  }

  &--secondary {
    background-color: var(--green-color);

    &:hover {
      background-color: var(--green-color);
    }

    &:active {
      background-color: var(--green-color);
    }
  }

  &--outline {
    fill: var(--main-color);
    background-color: transparent;
    color: var(--main-color);
    border: 1px solid var(--main-color);

    &:hover {
      background-color: transparent;
    }

    &:active {
      background-color: transparent;
    }
  }

  &__icon,
  &__icon-right {
    display: block;
    width: 24px;
    height: 24px;
    flex: 0 0 auto;
    fill: var(--white);
    margin-right: 10px;
  }

  &__icon-right {
    margin-left: 10px;
    margin-right: 0;
  }

  &--link {
    font-weight: 600;
    font-size: 16px;
    line-height: 16px;
    color: var(--main-color);
    fill: var(--main-color);
    padding: 0;
    background-color: transparent;

    &:hover,
    &:active {
      background-color: transparent;
    }
  }
}
