.what-have-slider {
  position: absolute;
  top: 0;
  right: 0;
  width: 960px;
  height: 712px;
  max-width: calc(100vw - 756px);
  @include desctop() {
    width: 690px;
    height: 511px;
    right: -122px;
    max-width: calc(100vw - 556px);
  }

  @include desctopSmall() {
    display: none;
  }

  &__item {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    opacity: 0;
    transition: opacity 0.3s;

    &.active {
      opacity: 1;
    }

    img {
      object-position: top;
      object-fit: contain;
      width: 100%;
      height: 100%;
    }
  }
}
