.action-plan {
  display: flex;
  align-items: stretch;
  min-height: 534px;

  @include ipad() {
    flex-direction: column;
  }

  &__cell {
    flex: 1 1 50%;
    display: flex;
    justify-content: flex-start;
    align-items: center;

    @include ipad() {
      flex: 1 1 auto;
    }
  }

  &__divider {
    position: relative;
    width: 200px;
    flex: 0 0 auto;

    @include desctop() {
      width: 165px;
    }

    @include ipad() {
      width: 100%;
      display: flex;
      align-items: center;
    }

    svg {
      position: absolute;
      width: 48px;
      height: 48px;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      fill: var(--main-color);

      @include ipad() {
        position: static;
        transform: rotate(90deg);
        flex: 0 0 auto;
        margin-right: calc(var(--gap) * 2);
      }
    }

    &:before,
    &:after {
      content: "";
      position: absolute;
      left: 50%;
      height: 40%;
      border-right: 1px solid var(--gray-color);
    }

    &:before {
      top: 0;

      @include ipad() {
        display: none;
      }
    }

    &:after {
      bottom: 0;

      @include ipad() {
        position: static;
        flex: 1 1 auto;
        height: 1px;
        border-right: none;
        border-top: 1px solid var(--gray-color);
      }
    }
  }

  &__item:not(:last-child) {
    margin-bottom: calc(var(--gap) * 4);
  }

  &__item-title {
    font-weight: 700;
    font-size: 16px;
    line-height: 24px;
    color: var(--main-color);
    margin-bottom: 6px;

    @include ipad() {
      font-size: 16px;
      line-height: 24px;
    }
  }

  &__item-text {
    font-weight: 700;
    font-size: 28px;
    line-height: 32px;

    @include ipad() {
      font-size: 24px;
      line-height: 36px;
    }
  }
}
