.cases {
  display: flex;
  justify-content: flex-end;

  .what-have__content {
    width: 657px;

    @include desctop() {
      width: 624px;
    }

    @include desctopSmall() {
      width: 100%;
    }
  }

  .what-have__title {
    @include desctop() {
      margin-bottom: calc(var(--gap) * 6);
    }

    @include desctopSmall() {
      margin-bottom: calc(var(--gap) * 2);
    }
  }

  .what-have__accordeon-title {
    margin-bottom: calc(var(--gap) * 3);
  }
}

.cases-slider.what-have-slider {
  right: auto;
  left: 0;
  max-width: calc(100vw - 856px);

  @include desctop() {
    left: -122px;
    max-width: calc(100vw - 600px);
  }
}
