.what-have {
  &__content {
    width: 592px;

    @include desctopSmall() {
      width: 100%;
    }
  }

  &__title {
    margin-bottom: calc(var(--gap) * 6);

    @include desctop() {
      margin-bottom: calc(var(--gap) * 4);
    }
  }

  &__text {
    margin-bottom: calc(var(--gap) * 6);
    max-width: 600px;

    @include desctopSmall() {
      margin-bottom: calc(var(--gap) * 3);
    }
  }

  &__text {
    margin-bottom: calc(var(--gap) * 6);
  }

  &__accordeon {
    padding-bottom: calc(var(--gap) * 3);
  }

  &__accordeon-item {
    padding: calc(var(--gap) * 3);
    margin-bottom: calc(var(--gap) * 2);
    border-radius: 20px;
    cursor: pointer;
    transition: 0.3s;

    @include desctopSmall() {
      display: flex;
    }

    @include ipad() {
      display: block;
    }

    &:last-child {
      margin-bottom: 0;
    }

    &.active {
      background-color: var(--white);
      box-shadow: 0px 4px 34px rgba(88, 77, 140, 0.36);

      .what-have__accordeon-icon {
        svg {
          fill: var(--main-color);
        }
      }

      .what-have__accordeon-text,
      .what-have__accordeon-sub-title {
        display: block;
      }

      .what-have__accordeon-link {
        display: flex;
      }

      .what-have__accordeon-title {
        color: var(--main-color);
      }

      .what-have__img-mobile {
        @include desctopSmall() {
          display: flex;
        }
      }
    }
  }

  &__accordeon-icon {
    width: 32px;
    height: 32px;
    flex: 0 0 auto;
    margin-right: calc(var(--gap) * 3);
    transition: 0.3s;

    svg {
      fill: #8c8c8c;
    }
  }

  &__accordeon-content {
    flex: 1 1 auto;
  }

  &__accordeon-title {
    font-weight: 600;
    font-size: 20px;
    line-height: 24px;
    margin-bottom: calc(var(--gap) * 0.5);
  }

  &__accordeon-sub-title {
    font-weight: 700;
    font-size: 16px;
    line-height: 24px;
    margin-bottom: 4px;
    display: none;
  }

  &__accordeon-text {
    display: none;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    margin-bottom: calc(var(--gap) * 3);
  }

  &__accordeon-link {
    display: none;
    align-items: center;

    svg {
      width: 24px;
      height: 24px;
      margin-left: calc(var(--gap) * 0.5);
    }
  }

  &__accordeon-wrapper {
    display: flex;
  }

  &__img-mobile {
    display: none;
    max-width: 400px;

    @include desctopSmall() {
      display: none;
      justify-content: flex-end;
      margin-top: 0;
      width: 100%;
      height: auto;
      margin-left: auto;

      img {
        position: relative;
        width: 100%;
        height: 100%;
        margin: 0 -24px 0 0;

        @include phone() {
          margin: 0 -24px;
          width: calc(100% + 48px);
        }
      }
    }

    @include ipad() {
      margin-top: calc(var(--gap) * 3);
    }
  }
}
