.button-mobile-menu {
  display: none;
  position: relative;
  width: 32px;
  height: 32px;
  padding: 3px;
  flex: 0 0 auto;

  &:hover {
    cursor: pointer;
  }

  @include laptop() {
    display: block;
    margin-left: calc(var(--gap) * 6);
  }

  @include phone() {
    margin-left: var(--gap);
  }

  &__el-1,
  &__el-2,
  &__el-3 {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 26px;
    height: 2px;
    border-radius: 2px;
    background-color: var(--main-color);
    transition: all 0.3s;
  }

  &__el-1 {
    top: 9px;
  }

  &__el-3 {
    top: auto;
    bottom: 9px;
    transform: translate(-50%, 50%);
  }
}

.active {
  .button-mobile-menu {
    &__el-2 {
      width: 0;
    }

    &__el-1 {
      transform: rotate(45deg) translate(-5px, 13px);
    }

    &__el-3 {
      transform: rotate(-45deg) translate(-5px, -13px);
    }
  }
}
