@function rem($px) {
  @return ($px/16px)+0rem;
}

@mixin desctopBig {
  @media screen and (max-width: 1880px) {
    @content;
  }
}

@mixin desctop {
  @media screen and (max-width: 1440px) {
    @content;
  }
}

@mixin desctopSmall {
  @media screen and (max-width: 1150px) {
    @content;
  }
}

@mixin laptop {
  @media screen and (max-width: 1024px) {
    @content;
  }
}

@mixin ipad {
  @media screen and (max-width: 768px) {
    @content;
  }
}

@mixin phone {
  @media screen and (max-width: 576px) {
    @content;
  }
}

