.nav {
  margin: 0 -16px;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  color: rgba(#545454, 0.6);

  @include desctop() {
    font-size: 13px;
  }

  @include laptop() {
    display: flex;
    flex-direction: column;
    position: fixed;
    top: 80px;
    left: 0;
    width: 100%;
    background-color: var(--white);
    margin: 0;
    height: calc(100% - 80px);
    transform: translate(-100%, 0);
    transition: transform 0.3s;
    visibility: hidden;

    &:after {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      height: 40px;
      background: linear-gradient(
        0deg,
        rgba(0, 0, 0, 0) 0%,
        rgba(0, 0, 0, 0.1) 100%
      );
    }
  }

  @include phone() {
    top: 53px;
    height: calc(100% - 53px);
  }

  &__list {
    display: flex;
    align-items: center;

    @include laptop() {
      flex: 1 1 auto;
      flex-direction: column;
      justify-content: space-between;
      padding: 8% 0;
      max-height: 600px;
    }
  }

  &__link {
    transition: 0.3s color;
    color: var(--black-color);
    white-space: nowrap;
  }

  &__item {
    padding: 0 calc(var(--gap) * 2);
    border-bottom: 2px solid rgba(#000, 0);

    &.active {
      .nav__link {
        color: var(--main-color);
      }
    }
  }
}

.active {
  .nav {
    @include laptop() {
      transform: translate(0, 0);
      visibility: visible;
    }
  }
}
