.title {
  font-weight: 700;
  font-size: 50px;
  line-height: 61px;
  color: var(--black-color);

  @include desctopSmall() {
    font-size: 35px;
    line-height: 45px;
  }

  @include ipad() {
    font-size: 24px;
    line-height: 36px;
  }

  &--white {
    color: var(--white);
  }

  &--center {
    text-align: center;
  }

  span {
    color: var(--main-color);
  }
}
