.footer {
  &__logo {
    display: block;
    fill: var(--white);
    margin-bottom: calc(var(--gap) * 4);
    width: 163px;
    height: 52px;
  }

  &__social {
    display: flex;
  }

  &__social-item {
    width: 32px;
    height: 32px;
    margin-right: calc(var(--gap) * 2);
  }

  &__social-link {
    display: block;
    width: 100%;
    height: 100%;
    fill: transparent;
    stroke: var(--white);
  }

  &__copyright {
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    opacity: 0.6;
  }

  &__row {
    margin-bottom: calc(var(--gap) * 6);

    @include ipad() {
      margin-bottom: calc(var(--gap) * 3);
    }
  }

  &__row-reverse {
    @include ipad() {
      flex-direction: column-reverse;
    }
  }

  &__col-link {
    @include ipad() {
      margin-bottom: var(--gap);
    }
  }
}
