.section-statistics {
  &__title {
    margin-bottom: calc(var(--gap) * 12);

    @include phone() {
      text-align: start;
      margin-bottom: calc(var(--gap) * 2);
    }
  }

  &__delimiter {
    display: flex;
    align-items: center;
    font-size: 20px;
    line-height: 30px;
    color: var(--white);
    padding: 0 calc(var(--gap) * 20);
    margin-bottom: calc(var(--gap) * 5);

    @include phone() {
      padding: 0;
      font-size: 16px;
      line-height: 24px;
      margin-bottom: calc(var(--gap) * 5.4);
    }

    div {
      flex: 0 0 auto;
      margin: 0 calc(var(--gap) * 4);

      @include phone() {
        margin: 0;
      }
    }

    &:after,
    &:before {
      content: "";
      flex: 1 1 auto;
      height: 2px;
      background-color: var(--white);
      opacity: 0.25;

      @include phone() {
        display: none;
      }
    }
  }

  &__expenses {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;

    @include ipad() {
      margin-bottom: calc(var(--gap) * 3);
    }
  }

  &__expenses-button {
    display: flex;
    align-items: center;
    height: 56px;
    font-weight: 700;
    font-size: 16px;
    line-height: 24px;
    fill: var(--white);
    color: var(--white);
    margin-bottom: calc(var(--gap) * 3);

    @include desctopSmall() {
      flex: 0 0 50%;
    }

    @include ipad() {
      flex: 0 0 100%;
      flex-direction: column;
      align-items: center;
      height: auto;
      margin-bottom: 0;
    }

    &:after {
      content: "+";
      font-weight: 700;
      font-size: 28px;
      line-height: 32px;
      padding: 0 calc(var(--gap) * 3);

      @include desctop() {
        padding: 0 calc(var(--gap) * 2);
      }
      @include phone() {
        line-height: 41px;
      }
    }

    &:last-child {
      &:after {
        content: "= x7";
        padding-right: 0;
        white-space: nowrap;
      }
    }
  }

  &__expenses-content {
    display: flex;
    align-items: center;
    height: 56px;
    border-radius: 50px;
    padding: calc(var(--gap) * 1) calc(var(--gap) * 2.5);
    border: 2px dashed rgba(255, 255, 255, 0.25);

    @include desctopSmall() {
      flex: 1 1 auto;
    }

    @include ipad() {
      align-self: stretch;
    }

     @include phone() {
      height: auto;

     }
  }

  &__expenses-icon {
    fill: var(--white);
    width: 24px;
    height: 24px;
    margin-right: calc(var(--gap) * 2);
  }
}
