.slider {
  max-width: 856px;
  margin: 0 auto;

  &__list {
    display: flex;
    margin-bottom: calc(var(--gap) * 3);
  }

  &__item {
    flex: 0 0 100%;
    width: 100%;
    font-weight: 400;
    font-size: 32px;
    line-height: 150%;

    @include desctopSmall() {
      font-size: 20px;
      line-height: 30px;
    }

    p:not(:last-child) {
      margin-bottom: calc(var(--gap) * 4);

      @include desctopSmall() {
        margin-bottom: calc(var(--gap) * 2);
      }
    }
  }

  &__footer {
    display: flex;
    align-items: center;
    justify-content: space-between;

    @include desctopSmall() {
      flex-direction: column;
      align-items: flex-start;
    }
  }

  &__authors {
    display: flex;
    width: 50%;
    @include desctopSmall() {
      width: 100%;
      margin-bottom: calc(var(--gap) * 3);
    }
  }

  &__author {
    display: flex !important;
    align-items: center;
    flex: 0 0 100%;
    width: 100%;
  }

  &__author-photo {
    width: 42px;
    height: 42px;
    flex: 0 0 auto;
    border-radius: 50%;
    overflow: hidden;
    margin-right: var(--gap);

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__author-name {
    font-weight: 700;
    font-size: 16px;
    line-height: 24px;
    margin-bottom: 2px;
  }
  &__author-position {
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
  }

  &__navigation {
    display: flex;
    align-items: center;

    @include desctopSmall() {
      width: 100%;
      justify-content: center;
    }
  }

  &__nav-buttons {
    display: flex;
  }

  &__progress {
    position: relative;
    width: 332px;
    height: 1px;
    background-color: var(--gray-color);
    div {
      display: none;
      position: absolute;
      top: 0;
      left: 0;
      height: 100%;
      width: 100%;
      background-color: rgba(#d9d9d9, 0.4);
      background-image: linear-gradient(to right, #5759ce, #5759ce);
      background-repeat: no-repeat;
      background-size: 0 100%;
      transition: background-size 0.4s ease-in-out;
    }

    @include desctopSmall() {
      display: none;
    }
  }

  &__nav-button {
    width: 32px;
    height: 32px;
    fill: var(--main-color);
    margin-left: var(--gap);
    cursor: pointer;

    &--right {
      transform: rotate(180deg);
    }
  }
}
