:root {
  --main-color: #5759ce;
  --black-color: #545454;
  --light-blue-color: #f3f3fd;
  --bg-dark-color: #232323;
  --bg-light-color: #f9f9fc;
  --orange-color: #fc773e;
  --red-color: #fe5656;
  --green-color: #44be84;
  --border-color: #e1dff4;
  --gray-color: #d9d9d9;
  --main-color-deep: #4547a8;
  --main-color-light: #6b6ef3;
  --bg-color: #fcfcff;
  --black-color-select: #f7f7ff;
  --white: #fff;
  --shadow-box: 0px 4px 34px rgba(123, 97, 255, 0.26);
  --shadow-box-white: 0px 4px 34px rgba(255, 255, 255, 0.26);
  --modal-shadow: 0px 4px 34px rgba(88, 77, 140, 0.36);
  --white-btn-shadow: 0px 0px 24px #3b3c8d;
  --shadow-header: 0px 4px 34px rgba(0, 0, 0, 0.26);
  --font-inter: "inter", Arial, sans-serif;
  --gap: 8px;
  --border-radius: 8px;
  --border-radius-big: 16px;
}
